import {
	mapGetters
} from 'vuex';
import Api from '@/common/api';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from '@/components/empty';
import {
	formatDate
} from '@/common/date';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			pageNum: 1,
			headerPosition: 'fixed',
			headerTop: '0px',
			loadingType: 'more', //加载更多状态
			navList: [{
					status: -1,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 2,
					text: '已发货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 3,
					text: '已完成',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 4,
					text: '申请退款',
					loadingType: 'more',
					orderList: []
				}
			]
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = this.navList.findIndex(v => v.status == options.status);

		this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.pageNum = 1;
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.pageNum = this.pageNum + 1;
		this.loadData();
	},
	computed: {
		...mapGetters(['hasLogin'])
	},
	methods: {
		//详情
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			uni.navigateTo({
				url: `/pages/order/orderDetail?orderSn=${item.orderSn}`
			});
		},
		//获取订单列表
		async loadData(type = 'add', loading) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let status = navItem.status;
			if (!this.hasLogin) {
				let url = '/pages/public/login';
				uni.navigateTo({
					url
				});
				return;
			}

			let params = {
				pageNum: this.pageNum,
				status: status
			};
			if (type === 'refresh') {
				navItem.orderList = [];
			}
			let data = await Api.apiCall('get', Api.order.orderList, params);
			const isfinish = navItem.orderList.length >= data.data.total;
			this.loadingType =isfinish? 'nomore' : 'more';
			if(isfinish) return ;
			let orderList = data.data.records;
			orderList=orderList.map(item=>{
				let obj= this.orderStateExp(item.orderStatus);
				console.log(obj)
				return Object.assign(obj,item);
			})
			console.log(orderList)
			navItem.orderList.push(...orderList);
			if (type === 'refresh') {
				if (loading == 1) {
					uni.hideLoading();
				} else {
					uni.stopPullDownRefresh();
				}
			}
		},

		//顶部tab点击
		tabClick(index) {
			this.pageNum = 1;
			this.tabCurrentIndex = index;
			this.loadData('refresh');
		},
		//删除订单
		async deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			});
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600);
		},
		async payOrder(item) {
			this.$common.navigateTo(
				'/pages/order/payment/index?type=1&order_id=' + item.orderSn
			)

		},
		//取消订单
		async cancelOrder(item) {
			let params = {
				orderId: item.id
			};
			this.showOrderModal(Api.order.closeOrder,params,"您确定要取消订单吗？",0)

		},
		showOrderModal(url,params,content,tabCurrentIndex){
			let _this=this;
			uni.showModal({
				cancelText:"关闭",
				confirmText:"确定",
				content:content,
				async complete(res) {
					if(res.confirm){
						let data = await Api.apiCall('post', url, params);
						if (data.code==0) {
							_this.tabCurrentIndex = tabCurrentIndex;
							_this.pageNum = 1;
							_this.loadData('refresh');
						}else{
							_this.$api.msg(data.msg)
						}
					}
				}
			})
		},
		//订单确认收货
		async confimDelivery(item) {
			let params = {
				orderId: item.id
			};
			this.showOrderModal(Api.order.confimDelivery,params,"确认收货？",4)

		},
		//订单申请退款
		async applyRefund(item) {
		let params = {
			orderId: item.id
		};
		this.showOrderModal(Api.order.applyRefund,params,"您确定要申请退款吗？",5)
		},
		// 去评价
		toEvaluate(orderSn) {
			this.$common.navigateTo(
				'/pages/order/orderDetail?orderSn=' + orderSn
			)
		},

		//订单状态文字和颜色
		orderStateExp(value) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			if (value === 0) {
				stateTipColor = '#909399';
				stateTip = '待付款';
			}else if (value === 1) {
				stateTipColor = '#909399';
				stateTip = '待发货';
			}else if (value === 2) {
				stateTip = '已发货';
			} else if (value === 3) {
				stateTip = '已完成';
			} else if (value === 4) {
				stateTip = '申请退款';
			} else if (value === 5) {
				stateTip = '无效订单';
			} else if (value === 6) {
				stateTipColor = '#909399';
				stateTip = '维权中';
			} else if (value === 7) {
				stateTip = ' 维权已完成';
			} else if (value === 8) {
				stateTip = '待分享';
			} else if (value === 13) {
				stateTip = '申请退款';
			} else if (value === 14) {
				stateTip = '已退款';
			} else if (value === 15) {
				stateTip = '已关闭';
			} else if (value === 16) {
				stateTip = '无效订单';
			} else {
				stateTip = '待付款';
			}

			return {
				stateTip,
				stateTipColor
			};
		},

		dateFormat(time) {
			if (time == null || time === '') {
				return 'N/A';
			}
			let date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd hh:mm:ss');
		}
	}
};
